<template>
  <div class="container-article">
    <!--  -->
    <el-card class="box-card">
      <!--    -->
      <div slot="header">
        <!--  -->
        <my-bread></my-bread>
      </div>
      <!--   -->
      <el-form label-width="80px" size="small">
        <el-form-item label="">
          <el-radio-group v-model="filterParams.status">
            <el-radio :label="null"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="4"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <!--  -->
          <!-- <my-channel :value="filterParams.channel_id" @input="filterParams.channel_id=$event"></my-channel> -->
          <my-channel v-model="filterParams.channel_id"></my-channel>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="dateArr"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            @change="changeDate"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search()"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!--  -->
    <el-card>
      <div slot="header"> {{total}} </div>
      <!--  -->
      <el-table :data="articles">
        <el-table-column label="">
          <template slot-scope="scope">
            <!--  -->
            <el-image :src="scope.row.cover.images[0]" style="width:150px;height:100px">
              <div slot="error">
                <img src="../../assets/error.gif" style="width:150px;height:100px" />
              </div>
            </el-image>
          </template>
        </el-table-column>
        <el-table-column label="" prop="title"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.status===0" type="info"></el-tag>
            <el-tag v-if="scope.row.status===1"></el-tag>
            <el-tag v-if="scope.row.status===2" type="success"></el-tag>
            <el-tag v-if="scope.row.status===3" type="warning"></el-tag>
            <el-tag v-if="scope.row.status===4" type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="pubdate"></el-table-column>
        <el-table-column label="" width="120px">
          <template slot-scope="scope">
            <el-button @click="toEdit(scope.row.id)" plain type="primary" icon="el-icon-edit" circle></el-button>
            <el-button @click="delArticle(scope.row.id)" plain type="danger" icon="el-icon-delete" circle></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        style="margin-top:20px;text-align:right"
        background
        layout="prev, pager, next"
        @current-change="changePager"
        :total="total"
        :current-page="filterParams.page"
        :page-size="filterParams.per_page"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      filterParams: {
        // null  
        status: null,
        channel_id: null,
        begin_pubdate: null,
        end_pubdate: null,
        page: 1,
        per_page: 20
      },
      // // 
      // channelOptions: [],
      // [,]
      dateArr: [],
      // 
      articles: [],
      // 
      total: 0
    }
  },
  created () {
    // this.getChannelOptions()
    this.getArticles()
  },
  methods: {
    // 
    toEdit (articleId) {
      this.$router.push(`/publish?id=${articleId}`)
    },
    // 
    async delArticle (articleId) {
      // 
      try {
        await this.$http.delete(`articles/${articleId}`)
        this.$message.success('')
        this.getArticles()
      } catch (e) {
        this.$message.error('')
      }
    },
    // // 
    // async getChannelOptions () {
    //   //  res = {data: {message:'',data: {channels:[]}}}
    //   //   
    //   const {
    //     data: { data }
    //   } = await this.$http.get('channels')
    //   this.channelOptions = data.channels
    // },
    // 
    async getArticles () {
      // post  body  axios.post(url, )
      // get     axios.get(url, {params: })
      const {
        data: { data }
      } = await this.$http.get('articles', { params: this.filterParams })
      this.articles = data.results
      // console.log(this.articles[0].id.toString())
      // 
      this.total = data.total_count
    },
    // 
    changePager (newPage) {
      this.filterParams.page = newPage
      this.getArticles()
    },
    // 
    search () {
      //  1
      this.filterParams.page = 1
      this.getArticles()
    },
    // 
    changeDate (value) {
      // value    this.dateArr 
      // if (!value) value = [null, null]
      if (value) {
        this.filterParams.begin_pubdate = value[0]
        this.filterParams.end_pubdate = value[1]
      } else {
        this.filterParams.begin_pubdate = null
        this.filterParams.end_pubdate = null
      }
    }
    // // 
    // changeChannel () {
    //   if (this.filterParams.channel_id === '') this.filterParams.channel_id = null
    // }
  }
}
</script>

<style scoped lang='less'>
.box-card {
  margin-bottom: 20px;
}
</style>
