<template>
  <div class="container-comment">
    <el-card>
      <div slot="header">
        <my-bread></my-bread>
      </div>
      <el-table :data="comments">
        <el-table-column label="" prop="title"></el-table-column>
        <el-table-column label="" prop="total_comment_count"></el-table-column>
        <el-table-column label="" prop="fans_comment_count"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">{{scope.row.comment_status?'':''}}</template>
        </el-table-column>
        <el-table-column label="" width="120px">
          <template slot-scope="scope">
            <el-button
              @click="toggleStatus(scope.row)"
              v-if="scope.row.comment_status"
              type="danger"
              size="small"
            ></el-button>
            <el-button @click="toggleStatus(scope.row)" v-else type="success" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        style="margin-top:20px"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="reqParams.page"
        :page-size="reqParams.per_page"
        @current-change="changePager"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      reqParams: {
        page: 1,
        per_page: 20,
        // 
        response_type: 'comment'
      },
      // 
      comments: [],
      // 
      total: 0
    }
  },
  created () {
    this.getComments()
  },
  methods: {
    // 
    toggleStatus (row) {
      let text = ''
      if (row.comment_status) {
        // 
        text = ''
      } else {
        // 
        text = ''
      }
      this.$confirm(text, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          const {
            data: { data }
          } = await this.$http.put(`comments/status?article_id=${row.id}`, {
            allow_comment: !row.comment_status
          })
          this.$message.success(
            data.allow_comment ? '' : ''
          )
          // row
          row.comment_status = data.allow_comment
        } catch (e) {
          this.$message.error('')
        }
      }).catch(() => {})
    },
    // 
    async getComments () {
      const {
        data: { data }
      } = await this.$http.get('articles', { params: this.reqParams })
      this.comments = data.results
      this.total = data.total_count
    },
    // 
    changePager (newPage) {
      this.reqParams.page = newPage
      this.getComments()
    }
  }
}
</script>

<style scoped lang='less'></style>
