<template>
  <div class="container-publish">
    <el-card>
      <div slot="header">
        <my-bread>{{$route.query.id?'':''}}</my-bread>
      </div>
      <!--  -->
      <el-form  ref="articleFormRef" :model="articleForm" :rules="articleFormRules" label-width="120px">
        <el-form-item label="" prop="title">
          <el-input v-model="articleForm.title" style="width:400px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <quill-editor @change="checkContent" v-model="articleForm.content" :options="editorOption" ></quill-editor>
        </el-form-item>
        <el-form-item label="" >
          <el-radio-group @change="articleForm.cover.images=[]" v-model="articleForm.cover.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <!--  -->
          <div v-if="articleForm.cover.type===1">
            <my-image v-model="articleForm.cover.images[0]"></my-image>
          </div>
          <div v-if="articleForm.cover.type===3">
            <my-image v-model="articleForm.cover.images[0]"></my-image>
            <my-image v-model="articleForm.cover.images[1]"></my-image>
            <my-image v-model="articleForm.cover.images[2]"></my-image>
          </div>
        </el-form-item>
        <el-form-item label="" prop="channel_id">
          <my-channel v-model="articleForm.channel_id"></my-channel>
        </el-form-item>
        <el-form-item v-if="$route.query.id">
          <el-button type="success" @click="editArticle()"></el-button>
        </el-form-item>
        <el-form-item v-else>
          <el-button type="primary" @click="addArticle(false)"></el-button>
          <el-button @click="addArticle(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// 
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
export default {
  components: {
    quillEditor
  },
  data () {
    return {
      // 
      articleForm: {
        // 
        title: null,
        // 
        cover: {
          type: 1,
          images: []
        },
        // ID
        channel_id: null,
        // 
        content: null
      },
      articleFormRules: {
        title: [
          { required: true, message: '' },
          // title5-30
          {
            min: 5,
            max: 30,
            message: '5-30'
          }
        ],
        content: [
          { required: true, message: '' }
        ],
        channel_id: [{ required: true, message: '' }]
      },
      // 
      editorOption: {
        placeholder: '',
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            ['blockquote', 'code-block'],
            [{ header: 1 }, { header: 2 }],
            [{ list: 'ordered' }, { list: 'bullet' }],
            [{ indent: '-1' }, { indent: '+1' }],
            ['image']
          ]
        }
      }
    }
  },
  created () {
    this.resetData()
  },
  watch: {
    //  ID 
    '$route.query.id' () {
      this.resetData()
    }
  },
  methods: {
    checkContent () {
      this.$refs.articleFormRef.validateField('content')
    },
    // (ID)
    resetData () {
      if (this.$route.query.id) {
        //   
        this.getArticle()
      } else {
        // 
        this.articleForm = {
        // 
          title: null,
          // 
          cover: {
            type: 1,
            images: []
          },
          // ID
          channel_id: null,
          // 
          content: null
        }
      }
    },
    //  
    async getArticle () {
      const { data: { data } } = await this.$http.get(`articles/${this.$route.query.id}`)
      this.articleForm = data
    },
    //  draft truefalse
    async addArticle (draft) {
      this.$refs.articleFormRef.validate(async valid => {
        if (!valid) return
        try {
          await this.$http.post(`articles?draft=${draft}`, this.articleForm)
          this.$message.success(draft ? '' : '')
          this.$router.push('/article')
        } catch (e) {
          this.$message.error('')
        }
      })
    },
    // 
    async editArticle () {
      this.$refs.articleFormRef.validate(async valid => {
        if (!valid) return
        try {
          await this.$http.put(`articles/${this.$route.query.id}?draft=false`, this.articleForm)
          this.$message.success('')
          this.$router.push('/article')
        } catch (e) {
          this.$message.error('')
        }
      })
    }
  }
}
</script>

<style scoped lang='less'></style>
