<script>
import store from '@/store'
import eventBus from '@/eventBus'
export default {
  data () {
    return {
      // 
      isOpen: true,
      // 
      name: '',
      // 
      photo: ''
    }
  },
  created () {
    // 
    const user = store.getUser()
    this.name = user.name
    this.photo = user.photo
    // 
    eventBus.$on('updateUserName', (name) => {
      this.name = name
    })
    eventBus.$on('updateUserPhoto', (photo) => {
      this.photo = photo
    })
  },
  methods: {
    toggleAside () {
      // 
      //   logo  
      this.isOpen = !this.isOpen
    },
    // 1. 
    // 2.  element-ui 
    // 3. htmlclick
    // 4.     .native 

    // 
    setting () {
      this.$router.push('/setting')
    },
    // 
    logout () {
      store.delUser()
      this.$router.push('/login')
    },
    // 
    handler (command) {
      // command   setting|logout
      //  setting    logout  
      // if (command === 'setting')
      // if (command === 'logout')
      // settingthis.setting() this['setting']()
      // logoutthis.logout() this['logout']()
      this[command]()
    }
  }
}
</script>

