<template>
  <div class="container-setting">
    <el-card>
      <div slot="header">
        <my-bread></my-bread>
      </div>
      <!--  -->
      <el-row>
        <el-col :span="12">
          <el-form ref="userForm" :rules="rules" :model="user" label-width="120px">
            <el-form-item label="">{{user.id}}</el-form-item>
            <el-form-item label="">{{user.mobile}}</el-form-item>
            <el-form-item prop="name" label="">
              <el-input v-model="user.name"></el-input>
            </el-form-item>
            <el-form-item prop="intro" label="">
              <el-input v-model="user.intro" type="textarea" :rows="3"></el-input>
            </el-form-item>
            <el-form-item prop="email" label="">
              <el-input v-model="user.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="updateUserInfo"></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12">
          <el-upload
            class="avatar-uploader"
            action=""
            :show-file-list="false"
            :http-request="updateUserPhoto"
          >
            <img v-if="user.photo" :src="user.photo" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <p style="text-align:center"></p>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
import eventBus from '@/eventBus'
export default {
  data () {
    return {
      // 
      user: {
        name: '',
        intro: '',
        email: '',
        photo: ''
      },
      rules: {
        name: [
          { required: true, message: '' },
          { min: 3, max: 5, message: ' 3  5 ' }
        ],
        intro: [
          { required: true, message: '' },
          { max: 50, message: '50' }
        ],
        email: [
          { required: true, message: '' },
          { type: 'email', message: '' }
        ]
      }
    }
  },
  created () {
    this.getUserInfo()
  },
  methods: {
    //   ({file:''}) ==> ({file}) 
    async updateUserPhoto ({ file }) {
      try {
        // 
        //  
        //  
        const formdata = new FormData()
        //   key
        formdata.append('photo', file)
        // axios
        const { data: { data } } = await this.$http.patch('user/photo', formdata)
        this.$message.success('')
        // 
        this.user.photo = data.photo
        // home
        eventBus.$emit('updateUserPhoto', data.photo)
      } catch (e) {
        this.$message.error('')
      }
    },
    // 
    async getUserInfo () {
      const { data: { data } } = await this.$http.get('user/profile')
      this.user = data
    },
    // 
    updateUserInfo () {
      this.$refs.userForm.validate(async (valid) => {
        if (!valid) return
        try {
          //    user 
          const { name, intro, email } = this.user
          await this.$http.patch('user/profile', { name, intro, email })
          this.$message.success('')
          // HOME
          eventBus.$emit('updateUserName', name)
        } catch (e) {
          this.$message.erro('')
        }
      })
    }
  }
}
</script>

<style scoped lang='less'></style>
