<template>
  <div class="container-image">
    <el-card>
      <div slot="header">
        <my-bread></my-bread>
      </div>
      <!--  -->
      <div class="btn_box">
        <el-radio-group @change="changeCollect" v-model="filterParams.collect" size="small">
          <el-radio-button :label="false"></el-radio-button>
          <el-radio-button :label="true"></el-radio-button>
        </el-radio-group>
        <el-button @click="openDialog()" style="float:right" type="success" size="small"></el-button>
      </div>
      <!--  -->
      <div class="img_list">
        <div class="img_item" v-for="item in images" :key="item.id">
          <img :src="item.url" alt />
          <div class="option" v-if="!filterParams.collect">
            <span
              @click="toggleStatus(item)"
              class="el-icon-star-off"
              :class="{red:item.is_collected}"
            ></span>
            <span @click="delImage(item.id)" class="el-icon-delete"></span>
          </div>
        </div>
      </div>
      <!--  -->
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :current-page="filterParams.page"
        :page-size="filterParams.per_page"
        @current-change="changePager"
        :hide-on-single-page="true"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="300px">
      <el-upload
        class="avatar-uploader"
        action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
        :headers="headers"
        :show-file-list="false"
        :on-success="uploadSuccess"
        name="image"
      >
        <img v-if="imageUrl" :src="imageUrl" class="avatar" />
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
    </el-dialog>
  </div>
</template>

<script>
import store from '@/store'
export default {
  data () {
    return {
      // 
      filterParams: {
        // false  true 
        collect: false,
        page: 1,
        per_page: 10
      },
      // 
      images: [],
      // 
      total: 0,
      // 
      dialogVisible: false,
      // 
      imageUrl: null,
      // headers
      headers: {
        Authorization: `Bearer ${store.getUser().token}`
      }
    }
  },
  created () {
    this.getImages()
  },
  methods: {
    // 
    uploadSuccess (res) {
      // console.log(res) === {data:{url:''}}
      // 
      this.imageUrl = res.data.url
      // 
      this.$message.success('')
      // 2s 
      window.setTimeout(() => {
        // 
        this.dialogVisible = false
        // 
        this.getImages()
      }, 2000)
    },
    // 
    openDialog () {
      this.dialogVisible = true
    },
    // 
    async delImage (id) {
      try {
        await this.$http.delete(`user/images/${id}`)
        this.$message.success('')
        this.getImages()
      } catch (e) {
        this.$message.error('')
      }
    },
    //  
    async toggleStatus (item) {
      try {
        // 
        const {
          data: { data }
        } = await this.$http.put(`user/images/${item.id}`, {
          collect: !item.is_collected
        })
        // 
        this.$message.success(data.collect ? '' : '')
        // 
        // is_collected 
        item.is_collected = data.collect
      } catch (e) {
        this.$message.error('')
      }
    },
    //  
    changeCollect () {
      this.filterParams.page = 1
      this.getImages()
    },
    // 
    changePager (newPage) {
      this.filterParams.page = newPage
      this.getImages()
    },
    // 
    async getImages () {
      const {
        data: { data }
      } = await this.$http.get('user/images', { params: this.filterParams })
      this.images = data.results
      this.total = data.total_count
    }
  }
}
</script>

<style scoped lang='less'>
.btn_box {
  margin-bottom: 20px;
}
.img_list {
  .img_item {
    width: 180px;
    height: 180px;
    border: 1px dashed #ddd;
    position: relative;
    display: inline-block;
    margin: 0 30px 20px;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
    .option {
      height: 30px;
      width: 100%;
      background: rgba(0, 0, 0, 0.3);
      position: absolute;
      left: 0;
      bottom: 0;
      text-align: center;
      line-height: 30px;
      span {
        color: #fff;
        margin: 0 20px;
      }
      .red {
        color: red;
      }
    }
  }
}
</style>
