<template>
  <div class="container-fans">
    <el-card>
      <div slot="header">
        <my-bread></my-bread>
      </div>
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="list">
          <div class="fans_list">
            <!-- item.id.toString() json-bigintid -->
            <div class="fans_item" v-for="item in fansList" :key="item.id.toString()">
              <!--  -->
              <el-avatar :size="80" :src="item.photo"></el-avatar>
              <!--  -->
              <p>{{item.name}}</p>
              <!--  -->
              <el-button type="primary" plain size="small">+</el-button>
            </div>
          </div>
          <el-pagination
            style="margin-top:20px"
            background
            layout="prev, pager, next"
            :total="total"
            :current-page="reqParams.page"
            :page-size="reqParams.per_page"
            @current-change="changePager"
          ></el-pagination>
        </el-tab-pane>
        <el-tab-pane label="" name="picture">
          <div ref="dom" style="width:600px;height:400px"></div>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  data () {
    return {
      // 
      fansList: [],
      // 
      reqParams: {
        page: 1,
        per_page: 21
      },
      // 
      total: 0,
      // 
      activeName: 'picture'
    }
  },
  mounted () {
    // echarts  dom
    const myChart = echarts.init(this.$refs.dom)
    const option = {
      color: ['#3398DB'],
      tooltip: {
        trigger: 'axis',
        axisPointer: { // 
          type: 'shadow' // 'line' | 'shadow'
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: [
        {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          axisTick: {
            alignWithLabel: true
          }
        }
      ],
      yAxis: [
        {
          type: 'value'
        }
      ],
      series: [
        {
          name: '',
          type: 'bar',
          barWidth: '60%',
          data: [10, 52, 200, 334, 390, 330, 220]
        }
      ]
    }
    myChart.setOption(option)
  },
  created () {
    this.getFansList()
  },
  methods: {
    async getFansList () {
      const {
        data: { data }
      } = await this.$http.get('followers', { params: this.reqParams })
      this.fansList = data.results
      this.total = data.total_count
    },
    // 
    changePager (newPage) {
      this.reqParams.page = newPage
      this.getFansList()
    }
  }
}
</script>

