<template>
  <div class="my-image">
    <!--  -->
    <div class="img_btn" @click="openDialog">
      <!-- ../assets/default.png  -->
      <img :src="value||defaultImage" alt />
    </div>
    <!--  -->
    <el-dialog :visible.sync="dialogVisible" width="700px">
      <!-- activeName tab -->
      <!-- label   el-tab-pane -->
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="image">
          <!--  -->
          <el-radio-group @change="changeCollect" v-model="filterParams.collect" size="small">
            <el-radio-button :label="false"></el-radio-button>
            <el-radio-button :label="true"></el-radio-button>
          </el-radio-group>
          <!--  -->
          <div class="img_list">
            <div
              class="img_item"
              :class="{selected:selectedImageUrl===item.url}"
              v-for="item in images"
              :key="item.id"
            >
              <img @click="selectedImage(item.url)" :src="item.url" alt />
            </div>
          </div>
          <!--  -->
          <el-pagination
            background
            layout="prev, pager, next"
            :total="total"
            :current-page="filterParams.page"
            :page-size="filterParams.per_page"
            @current-change="changePager"
            :hide-on-single-page="true"
          ></el-pagination>
        </el-tab-pane>
        <el-tab-pane label="" name="upload">
          <el-upload
            class="avatar-uploader"
            action="http://ttapi.research.itcast.cn/mp/v1_0/user/images"
            :headers="headers"
            :show-file-list="false"
            :on-success="uploadSuccess"
            name="image"
          >
            <img v-if="uploadImageUrl" :src="uploadImageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="confirmImage"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import store from '@/store'
import defaultImage from '../assets/default.png'
export default {
  name: 'my-image',
  props: ['value'],
  data () {
    return {
      // 
      dialogVisible: false,
      // name
      activeName: 'image',
      // 
      filterParams: {
        collect: false,
        page: 1,
        per_page: 8
      },
      // 
      total: 0,
      // 
      images: [],
      // 
      selectedImageUrl: null,
      // 
      headers: {
        Authorization: `Bearer ${store.getUser().token}`
      },
      // 
      uploadImageUrl: null,
      // 
      // confirmImageUrl: defaultImage,
      // 
      defaultImage
    }
  },
  methods: {
    // 
    confirmImage () {
      if (this.activeName === 'image') {
        // 
        if (!this.selectedImageUrl) return this.$message.warning('')
        // 
        // this.confirmImageUrl = this.selectedImageUrl
        // 
        this.$emit('input', this.selectedImageUrl)
      } else {
        // 
        if (!this.uploadImageUrl) return this.$message.warning('')
        // 
        // this.confirmImageUrl = this.uploadImageUrl
        // 
        this.$emit('input', this.uploadImageUrl)
      }
      // 
      this.dialogVisible = false
    },
    // 
    uploadSuccess (res) {
      this.uploadImageUrl = res.data.url
    },
    // 
    selectedImage (url) {
      this.selectedImageUrl = url
    },
    // 
    openDialog () {
      this.dialogVisible = true
      this.getImages()
      // - 
      this.selectedImageUrl = null
      // - 
      this.uploadImageUrl = null
      // - tab  
      this.activeName = 'image'
    },
    //  
    changeCollect () {
      this.filterParams.page = 1
      this.getImages()
    },
    // 
    async getImages () {
      const {
        data: { data }
      } = await this.$http.get('user/images', { params: this.filterParams })
      this.images = data.results
      this.total = data.total_count
    },
    // 
    changePager (newPage) {
      this.filterParams.page = newPage
      this.getImages()
    }
  }
}
</script>

<style scoped lang='less'>
.my-image {
  display: inline-block;
  margin-right: 20px;
  .img_btn {
    width: 150px;
    height: 150px;
    border: 1px dashed #ddd;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
}
.img_list {
  margin-top: 10px;
  .img_item {
    width: 150px;
    height: 120px;
    border: 1px dashed #ddd;
    position: relative;
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
    // less  &
    // a.active{}  
    // a{ .active{} }  ===> a .active{}
    // a{ &.active{} }  ===> a.active{}
    &.selected {
      &::after {
        // .img_item.selected::after{} selected
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: rgba(0, 0, 0, 0.3) url(../assets/selected.png) no-repeat
          center / 50px;
      }
    }
  }
}
</style>
